import React, { useState, useEffect } from "react";
import { Free, Buzy, Next,Booking } from "../components/RoomDisplay";
import { getRoom,getRoomBookingsForToday } from "../logic/intranets-api";

const RoomDisplay = (props) => {
  useEffect(() => {
    async function run() {
      getRoomBookingsForToday(props.email).then(data=>setRoomBookings(data.value))
      getRoom(props.email).then(room=>setRoom(room))
      
    }
    run();
  }, [props.email]);
  const [room, setRoom] = useState({});
  const [roomBookings, setRoomBookings] = useState([]);
  return (
    <div>
      Room {room.title}
      <Free></Free>
      <Buzy {...room}></Buzy>
      <Next></Next>
      {roomBookings.map(booking=>{
          return <Booking {...booking}/>
          
      })}
    </div>
  );
};

export default RoomDisplay;
