import React from 'react';
import "./display.css"
import moment from "moment"
export const Buzy = (props) => {
    return (  <div className="buzy-container">{props.title}
    <div className="buzy-container-status-red">

    </div>
    </div>);
}

export const Free = () => {
    return (  <div>Free</div>);
}

export const Next = () => {
    return (  <div>Next</div>);
}

export const Booking = (props) => {
    return (  <div>
        <div>{props.organizer.emailAddress.name} ({props.organizer.emailAddress.address})</div>
        <div style={{display:"flex"}}><div style={{minWidth:"100px"}}>From: </div><div>{moment(props.start.data).format()}</div></div>
        <div style={{display:"flex"}}><div style={{minWidth:"100px"}}>To: </div><div>{moment(props.end.data).format()}</div></div>
        
        </div>); 
}

